export function bodyLock() {
  const bodyRef = document.querySelector('body');
  const bodyStyle = window.getComputedStyle(bodyRef);
  const bodyWidth =
    bodyRef.offsetWidth +
    parseInt(bodyStyle.marginLeft) +
    parseInt(bodyStyle.marginRight); //      

  const widthVerticalScrollBar = window.innerWidth - bodyWidth; //   

  document.body.style.overflow = 'hidden';
  document.body.style.paddingRight = widthVerticalScrollBar + 'px'; //      
}

export function bodyUnlock(time) {
  setTimeout(() => {
    document.body.style.paddingRight = '';
    document.body.style.overflow = 'auto';
  }, time); //         
}
